<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process83"></a>

<p><br></p>
<p><span class="rvts17"></span><span class="rvts50">Blank Skill Books</span><span class="rvts17"></span>
<br><span class="rvts21"></span><span class="rvts20">2</span><span class="rvts21"></span>
<br><span class="rvts21"></span>
<br><span class="rvts18"><br></span></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



